<template>
    <div class="userDapen">
       <mu-container>
         <mu-card class="itemList" @click="tourl({path:'/home/install/userdapen/deviceList'})">
           <mu-ripple color="green">
             <p>大棚名称:XXXXX</p><br>
             <p>大棚地址:XXXXX</p><br>
             <p>地址:XXXXX</p><br>
             <p>长度:XXXXX</p><br>
             <p>高度:XXXXX</p><br>
             <p>宽度:XXXXX</p>
           </mu-ripple>
         </mu-card>
       </mu-container>
      <div>
        <div class="addDapen" @click="tourl({path:'/home/install/userdapen/add'})">
          <mu-ripple color="black">
           + 添加大棚
          </mu-ripple>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "userDapen",
        methods:{
          tourl(url){
            this.$router.push(url)
          }
        }
    }
</script>

<style scoped lang="stylus">
  .userDapen
    padding-top 55px
    padding-bottom 50px
    .itemList
        padding 10px
        margin-top 10px
    .addDapen
      background:white
      position fixed
      bottom 0
      width 100%
      height 40px
      line-height 40px
      text-align center
</style>



