<template>
  <div class="author">
    正在授权登录...
  </div>
</template>
<script>
  import {getToken,getuserInfo} from 'api/userApi'
  export default {
    name:'author',
    data(){
      return {
        token:'',
        authorUrl:''
      };
    },
    created(){
      let token=window.localStorage.getItem('token')  //登录状态status
      alert(token+'本地token');
      // 检测会员有没有登录的状态
      if(!token){
        //未检测到token

        this._getauthorUrl();


      }else{

        // 如果有token 但是vuex中没有用户登录信息则做登录操作
        this.login();//获取登录信息

      }
    },
    methods: {
      login () {
        //获取用户信息
        let token=window.localStorage.getItem('token');
        getuserInfo(token).then((res)=>{
            alert(res.data.openid+'服务器存储的openid');
          if(res.ret==0){
            var openid=res.data.openid
            if(openid){
              //有openid 进入到刚开始想进入的页面中
              this.$store.state.userid=res.data.user_id;
              this.$store.state.openid=res.data.openid;
              this.$store.state.userInfo=res.data;
              alert(this.$store.state.userInfo+'状态管理中的用户信息');
              setTimeout(() => {
                this.goBeforeLoginUrl();// 页面恢复(进入用户一开始请求的页面)
              }, 20)

            }else{
              //没有openid重新去开启授权
              window.localStorage.setItem('token','');
              this._getauthorUrl();
            }

          }else{
            window.localStorage.setItem('token','');
            this._getauthorUrl();
          }

        })


      },
      //获取token
      _getauthorUrl(){
        var _this=this
        getToken().then((res)=>{
          if(res.ret==0){
            _this.authorUrl=res.data.url
            window.localStorage.setItem('token',res.data.token) //存储token
            alert(res.data.token+'服务器token')
            alert(_this.authorUrl+'微信授权地址')
            if(_this.authorUrl!=''){
              window.location.href=_this.authorUrl
            }else{
              alert(res.msg)
            }
          }else{
            alert(res.msg)
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .author
    font-size 20px
</style>



