// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import $ from 'jquery'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'
import "lib-flexible/flexible"
import FastClick from 'fastclick'
import museUi from 'muse-ui';
import Loading from 'base/lib/index'
import Message from 'base/lib/Mssage.js'
import "common/css/reset.css";
import "muse-ui/dist/muse-ui.css";
import "common/font/iconfont.css";
window.$=$
FastClick.attach(document.body);
Vue.use(museUi);
Vue.use(Loading);
Vue.use(Message);

//授权成功后的数据清理
Vue.prototype.goBeforeLoginUrl = () => {
  let url = window.localStorage.getItem('beforeLoginUrl')
  if(!url || url.indexOf('/author') != -1){
    router.push('/home')
  }else{
    if(url == '/'){
        url = '/home'
    }
    window.localStorage.setItem('beforeLoginUrl', '');//清空浏览器缓存url
    window.localStorage.setItem('token', '');//清空浏览器缓存token
    router.push(url);//进入想进的页面
  }
}
//ajax全局拦截器
$.ajaxSetup({
  timeout:10000,
  beforeSend(xhr){
      Vue.$loading.show();
  },
  complete:function(xhr,status){
      Vue.$loading.hide();
  },
  success:function(result){
      if(result.ret==0){
        Vue.$loading.hide();
      }else{
        Vue.$loading.hide();
        Vue.$message.show(result.msg);
      }
  },
  error:function (xhr,status,error){
     console.log(error+'网络请求错误'+xhr.status);
  }
});




//全局路由守卫
router.beforeEach((to, from, next) => {
  let openid = store.state.openid;
  //关闭授权
  // let openid=true;
  if(!openid && to.path != '/author'){
    // 第一次进入项目
    window.localStorage.setItem("beforeLoginUrl", to.fullPath);//存储url授权成功进入的url
    router.push('/author');
    return false
  }
  //授权后点击返回回到首页，防止用户重复授权
  if(to.path == '/author' && openid){
    // 用户使用后退返回到授权页，则默认回到首页
    next('/home')
    return false
  }
  next();
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



