<template>
    <div class="addDevice">
      <mu-container>
        <mu-card class="dev-box">
          <mu-text-field v-model="devnmae" label="设备名称"  label-float></mu-text-field><br>
          <mu-text-field v-model="devwidth" label="设备ID"  label-float></mu-text-field><br>
          <mu-select label="设备类型" v-model="devtype" label-float>
            <mu-option v-for="option,index in options" :key="option" :label="option" :value="option" color="yellow"></mu-option>
          </mu-select>
          <mu-button  color="primary" style="width:60%" class="submit" :disabled="false">确认添加</mu-button>
        </mu-card>
      </mu-container>
    </div>
</template>
<script>
    export default {
        name: "addDevice",
        data(){
          return{
            options: [
              '',
              'Option 1', 'Option 2', 'Option 3', 'Option 4',
              'Option 5', 'Option 6', 'Option 7', 'Option 8',
              'Option 9', 'Option 10'
            ],
            devnmae:'',
            devwidth:'',
            devtype:''
          }
        },
        created(){
         this.$message.show('成功',"error");
        }
    }
</script>

<style scoped lang="stylus">
  .addDevice
     padding-top 55px
     .dev-box
        margin-top 10px
        padding-top 20px
        text-align center
        padding-bottom  20px

</style>



