<template>
  <mu-fade-transition>
  <div class="loading-mask" v-show="show">
  <div class="roataqx-loader">
    <div class="message">{{text}}</div>
    <div class="one"></div>
    <div class="two"></div>
    <div class="three"></div>
  </div>
  </div>
  </mu-fade-transition>
  <!--<div class="loading-box" v-show="show">-->
    <!--<div class="loading-mask"></div>-->
    <!--<div class="loading-content">-->
      <!--<div class="animate">-->
      <!--</div>-->
      <!--<div class="text">{{text}}</div>-->
    <!--</div>-->
  <!--</div>-->
</template>

<script>
  export default {
    props: {
      show: Boolean,
      text: {
        type: String,
        default: '正在加载...'
      },
    }
  }
</script>

<style>
  .roataqx-loader {
    width: 69px;
    height: 69px;
    border-radius: 50%;
    perspective: 800px;
    position:absolute;
    top:50%;
    left:50%;
    margin-left:-34.5px;
    margin-top:-34.5px;
  }
  .roataqx-loader .message{
    position:absolute;
    bottom:-70px;
    color: white;
    text-align: center;
  }
  .roataqx-loader > div {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  .roataqx-loader .one {
    left: 0%;
    top: 0%;
    animation: rotate-one 1.4s linear infinite;
    border-bottom: 3px solid white;
  }

  .roataqx-loader .two {
    right: 0%;
    top: 0%;
    animation: rotate-two 1.4s linear infinite;
    border-right: 3px solid #2FE01B;
  }

  .roataqx-loader .three {
    right: 0%;
    bottom: 0%;
    animation: rotate-three 1.4s linear infinite;
    border-top: 3px solid red;
  }

  @keyframes rotate-one {
    0% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    }
  }

  @keyframes rotate-two {
    0% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    }
  }

  @keyframes rotate-three {
    0% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
    }
    100% {
      transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    }
  }
  /*.loading-box {*/
    /*position: relative;*/
    /*text-align: center;*/
  /*}*/
  .loading-mask {
    position:absolute;
    background:rgba(0,0,0,0.5);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index:999;
  }
  /*.loading-content {*/
    /*position: fixed;*/
    /*left: 50%;*/
    /*top: 250px;*/
    /*width:200px;*/
    /*min-height: 120px;*/
    /*margin-left: -100px;*/
    /*padding: 10px;*/
    /*background: hsla(0,0%,7%,.7);*/
    /*text-align: center;*/
    /*border-radius: 5px;*/
    /*color: #fff;*/
    /*z-index: 5001;*/
  /*}*/
  /*.loading-content .animate {*/
    /*width: 40px;*/
    /*height: 40px;*/
    /*vertical-align: baseline;*/
    /*display: inline-block;*/
    /*margin: 25px 0 10px;;*/
    /*vertical-align: middle;*/
    /*animation: superLoading 1s steps(12, end) infinite;*/
    /*background: transparent url("data:image/svg+xml;base64,") no-repeat;*/
    /*background-size: 100%;*/
  /*}*/
  /*.loading-content .text {*/
    /*margin: 0 0 15px;*/
    /*font-size: 16px;*/
  /*}*/
  /*@-webkit-keyframes superLoading {*/
    /*0% {*/
      /*transform: rotate3d(0, 0, 1, 0deg);*/
    /*}*/
    /*100% {*/
      /*transform: rotate3d(0, 0, 1, 360deg);*/
    /*}*/
  /*}*/
  /*@keyframes superLoading {*/
    /*0% {*/
      /*transform: rotate3d(0, 0, 1, 0deg);*/
    /*}*/
    /*100% {*/
      /*transform: rotate3d(0, 0, 1, 360deg);*/
    /*}*/
  /*}*/
</style>



