<template>
    <div class="settingList">
      <mu-container>
        <mu-card class="itemselect">
          <mu-tabs :value.sync="value" color="green"  full-width>
          <mu-tab>手动</mu-tab>
          <mu-tab>定时</mu-tab>
          </mu-tabs>
          <component v-bind:is="currentTabComponent"></component>
          </mu-card>
          <mu-card @click="tourl({path:'/home/pengList/details/equipment',query:{}})">
        </mu-card>
      </mu-container>
    </div>
</template>

<script>
  import Manual from './chilren/Manual'
  import Timeing from "./chilren/settime"
    export default {
        name: "settingList",
        data(){
          return {
            componentList: ['Manual', 'Timeing'],
            value: 1
          }
        },
      methods: {
        tourl(url) {
          this.$router.push(url)
        }
      },
      computed: {
        currentTabComponent() {
          return this.componentList[this.value]
        }
      },
      created(){

      },
      components: {
        Manual,
        Timeing
      },
      beforeRouteEnter (to, from, next) {
          to.meta.title=to.query.title;
          next();
      },

    }
</script>

<style scoped lang="stylus">
 .settingList
   padding-top 70px
</style>



